/* Header Styles */
.main-header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 1000;
  background: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  padding: 1.5rem 0;
}

/* Reset all link styles */
a {
  color: inherit;
  text-decoration: none;
}

a:active,
a:focus,
a:visited,
a:hover {
  color: inherit;
  text-decoration: none;
}

.header-container {
  max-width: 1600px;
  width: 100%;
  margin: 0 auto;
  padding: 0 5%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-sizing: border-box;
}

.logo h1 {
  font-family: 'Canopee', serif;
  font-size: 2.5rem;
  font-weight: 900;
  letter-spacing: 0px;
  color: #000;
  margin: 0;
  text-transform: uppercase;
  -webkit-text-stroke: 1px #000000;
  -webkit-text-fill-color: transparent;
  transition: all 0.3s ease;
}

.main-nav {
  display: flex;
  gap: 3rem;
  padding-right: 5%;
}

.logo {
  padding-left: 5%;
}

.nav-link {
  font-family: 'Inter', sans-serif;
  font-size: 0.9rem;
  font-weight: 500;
  color: #000;
  text-decoration: none;
  text-transform: uppercase;
  letter-spacing: 1px;
  position: relative;
  padding: 0.5rem 0;
  transition: color 0.3s ease;
}

/* Remove default link styles */
.nav-link:active,
.nav-link:focus,
.nav-link:visited {
  color: #000;
  text-decoration: none;
}

.nav-link::after {
  content: '';
  position: absolute;
  width: 0;
  height: 2px;
  bottom: 0;
  left: 0;
  background-color: #000;
  transition: width 0.3s ease;
}

.nav-link:hover {
  color: #333;
}

.nav-link:hover::after {
  width: 100%;
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .header-container {
    flex-direction: column;
    gap: 1rem;
  }
  
  .main-nav {
    width: 100%;
    justify-content: space-between;
    gap: 1rem;
  }
  
  .nav-link {
    font-size: 0.8rem;
  }
}
