@import url('https://fonts.googleapis.com/css2?family=Canopee&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600&display=swap');

.unselectable {
   -moz-user-select: -moz-none;
   -khtml-user-select: none;
   -webkit-user-select: none;
   -ms-user-select: none;
   user-select: none;
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.aboutme-main {
  background: #f3f3f3;
  color: #111;
  min-height: 40vh;
  width: 50%;
  margin: 200px auto 0;
  padding: 4rem 5%;
  display: flex;
  flex-direction: column;
  gap: 3rem;
  box-sizing: border-box;
  border-radius: 16px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.05);
  opacity: 0;
  animation: fadeIn 0.6s ease-out forwards;
  animation-delay: 0.2s;
}

.aboutme-hero {
  text-align: center;
  max-width: 800px;
  margin: 0 auto;
  padding: 0 20px;
  opacity: 0;
  transform: translateY(20px);
  animation: fadeInUp 0.6s ease-out forwards;
  animation-delay: 0.4s;
}

.aboutme-title {
  font-family: 'Canopee', serif;
  font-size: clamp(2.2rem, 5vw, 3.5rem);
  font-weight: 400;
  margin: 0 0 1.25rem 0;
  letter-spacing: -0.02em;
  line-height: 1.1;
  opacity: 0;
  transform: translateY(20px);
  animation: fadeInUp 0.6s ease-out forwards;
  animation-delay: 0.6s;
}

.aboutme-body {
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
  font-size: 1.1rem;
  color: #222;
  line-height: 1.7;
  max-width: 700px;
  margin: 0 auto 0.5rem auto;
  font-weight: 400;
  padding: 0 1rem;
  opacity: 0;
  transform: translateY(20px);
  animation: fadeInUp 0.6s ease-out forwards;
  animation-delay: 0.8s;
}

.aboutme-details {
  text-align: left;
  max-width: 700px;
  margin: 0 auto;
  padding: 0 20px;
  opacity: 0;
  transform: translateY(20px);
  animation: fadeInUp 0.6s ease-out forwards;
  animation-delay: 1s;
}

.aboutme-details h2 {
  font-family: 'Canopee', serif;
  font-size: 1.8rem;
  font-weight: 400;
  margin: 2.5rem 0 1rem 0;
  letter-spacing: -0.02em;
  line-height: 1.2;
  opacity: 0;
  transform: translateY(20px);
  animation: fadeInUp 0.6s ease-out forwards;
  animation-delay: 1.2s;
}

.aboutme-details p {
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
  font-size: 1.1rem;
  color: #222;
  line-height: 1.7;
  margin: 0 0 1.5rem 0;
  padding: 0;
  opacity: 0;
  transform: translateY(20px);
  animation: fadeInUp 0.6s ease-out forwards;
  animation-delay: 1.4s;
}
